html {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
}

body {
  background: var(--body-bg);
  position: relative;
  overflow-x: hidden;
  width: 100%;
  min-height: -webkit-fill-available;
  margin: 0 auto;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  font-family: var(--font-family);
  color: rgba(var(--cl-b), 1);
  counter-reset: step-counter;
  -webkit-text-size-adjust: 100%;
}

main {
  margin: 0 auto;
  position: relative;
  z-index: 1;
  padding-top: var(--header-height);
  min-width: 0 !important;
}
main.no-padding {
  padding: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  display: inline-flex;
  margin: 0;
  font-style: inherit;
  font-weight: inherit;
  font-family: inherit;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

button {
  display: inline-flex;
  margin: 0;
  padding: 0;
  line-height: initial;
  color: inherit;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

p {
  font-size: 15px;
  font-family: var(--font-family);
  font-weight: 400;
  color: rgba(var(--cl-b), 1);
  font-style: normal;
  line-height: 130%;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

picture {
  display: inline-flex;
}

label {
  display: inline-flex;
}

input {
  width: 100%;
  background-color: transparent;
  border: none;
  outline: none;
}
input::placeholder {
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

textarea {
  border: none;
  outline: none;
  resize: none;
}

input[type=number] {
  appearance: textfield;
}

.just-validate-error-label {
  position: absolute;
  bottom: 0;
  left: 50%;
  white-space: nowrap;
  transform: translate(-50%, 100%);
  font-size: 12px;
}

.container {
  position: relative;
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
  padding: 0 var(--container-offset);
}
.container--mode {
  max-width: var(--container-width--mode);
}

.panel,
[data-content] {
  z-index: 20;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s;
}

.main-inner {
  padding: clamp(50px, 4.6154vw, 60px) 0;
}
.main-inner__box {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 309px 1fr;
}
@media (max-width: 1024px) {
  .main-inner__box {
    grid-template-columns: 1fr;
  }
}

.dis-scroll {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  overscroll-behavior: none !important;
  scroll-behavior: none !important;
}

*::-webkit-scrollbar {
  width: 8px;
  height: 7px;
  cursor: pointer;
}
@media (max-width: 767px) {
  *::-webkit-scrollbar {
    display: none;
  }
}
*::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-thumb);
  cursor: pointer;
  transition: all 0.2s;
}
*::-webkit-scrollbar-track {
  background-color: var(--scrollbar-track);
}
*::-webkit-scrollbar-button {
  display: none;
  background-color: transparent;
  box-shadow: none;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.modal {
  margin: auto;
  transform: scale(0.8);
  display: none;
}
.modal.active {
  transform: scale(1);
}
.modal__close {
  position: absolute;
  right: 20px;
  top: 20px;
  z-index: 20;
}
@media (max-width: 576px) {
  .modal__close {
    right: 12px;
    top: 12px;
  }
}
.modal__close svg {
  transition: all 0.4s;
  stroke: var(--svg-color-k);
}
.modal__close:hover svg {
  stroke: rgba(var(--bg-c), 1);
}
.modal .close svg {
  pointer-events: none;
}

.cookies-inner {
  z-index: 20;
  position: fixed;
  right: var(--container-offset);
  bottom: 50px;
  transition: all 0.4s;
  transform: translateX(0);
}
.cookies-inner.active {
  transform: translateX(150%);
}

