}
@media (min-width: 576px) {
  .info-section .title {
    max-width: min(80.7692vw, 1050px);
  }
}
.info-section__wrapp {
  display: flex;
  flex-direction: column;
  grid-gap: clamp(24px, 2.8462vw, 37px);
}
.info-section__wrapp .info-section__title {
  margin-bottom: 2px;
}
.info-section__wrapp--mode {
  margin-top: 3px;
}
.info-section__images {
  display: grid;
  grid-gap: clamp(24px, 2.7692vw, 36px);
}
@media (min-width: 576px) {
  .info-section__images {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 576px) {
  .info-section__images {
    grid-template-columns: 100%;
  }
  .info-section__images .info-section__image:last-child {
    display: none;
  }
}
.info-section__content {
  margin-top: clamp(10px, 1.7692vw, 23px);
  display: grid;
}
@media (min-width: 1024px) {
  .info-section__content {
    grid-gap: 24px clamp(30px, 6.5385vw, 85px);
  }
}
@media (max-width: 1024px) {
  .info-section__content {
    grid-gap: 24px;
  }
}
@media (min-width: 768px) {
  .info-section__content {
    grid-template-columns: auto 1fr;
  }
}
@media (max-width: 767px) {
  .info-section__content {
    grid-template-columns: 100%;
  }
}

.aside-menu {
  background: rgba(var(--bg-c), 1);
  border: 1px solid var(--br-a);
  border-radius: 10px;
  height: fit-content;
  position: relative;
  z-index: 5;
}
@media (min-width: 1024px) {
  .aside-menu {
    overflow: hidden;
  }
}
.aside-menu.active {
  z-index: 22;
}
@media (min-width: 1024px) {
  .aside-menu__top {
    padding: clamp(15px, 1.5385vw, 20px) clamp(20px, 1.9231vw, 25px);
    border-bottom: 1px solid var(--br-a);
  }
}
.aside-menu .subtitle {
  font-size: clamp(18px, 1.6923vw, 22px);
}
@media (max-width: 1024px) {
  .aside-menu .subtitle {
    display: none;
  }
}
.aside-menu__btn {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: clamp(18px, 1.6923vw, 22px);
  line-height: 120%;
  text-transform: uppercase;
  color: rgba(var(--cl-b), 1);
  display: flex;
  align-items: center;
  grid-gap: 10px;
  justify-content: space-between;
  width: 100%;
  padding: clamp(15px, 1.5385vw, 20px) clamp(20px, 1.9231vw, 25px);
}
.aside-menu__btn svg {
  transition: all 0.4s;
}
.aside-menu__btn.active svg {
  transform: scaleY(-1);
}
@media (min-width: 1024px) {
  .aside-menu__btn {
    display: none;
  }
}
.aside-menu__list {
  display: flex;
  flex-direction: column;
}
@media (max-width: 1024px) {
  .aside-menu__list {
    position: absolute;
    bottom: -10px;
    left: -1px;
    width: 100%;
    background: rgba(var(--bg-c), 1);
    transform: translateY(0);
    border-radius: 10px;
    z-index: 10;
    border: 1px solid var(--br-a);
    box-shadow: 5px 5px 15px 2px rgba(0, 0, 0, 0.43);
    display: none;
    pointer-events: none;
    opacity: 0;
    visibility: hidden;
    transition: all 0.6s ease;
  }
}
@media (max-width: 1024px) and (min-width: 576px) {
  .aside-menu__list {
    max-width: 319px;
  }
}
@media (max-width: 1024px) and (max-width: 576px) {
  .aside-menu__list {
    width: calc(100% + 2px);
  }
}
@media (max-width: 1024px) {
  .aside-menu__list.active {
    transform: translateY(100%);
    display: flex;
    opacity: 1;
    visibility: visible;
    pointer-events: all;
  }
}
.aside-menu__item:last-child .aside-menu__link {
  border-bottom: none;
}
.aside-menu__link {
  padding: 10px clamp(20px, 1.9231vw, 25px);
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: 10px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 15px;
  line-height: 147%;
  color: rgba(var(--cl-b), 1);
  transition: all 0.4s;
  border-bottom: 1px solid var(--br-a);
  width: 100%;
}
.aside-menu__link svg {
  transition: all 0.4s;
  fill: rgba(var(--cl-b), 1);
}
.aside-menu__link:hover {
  color: rgba(var(--cl-a), 1);
}
.aside-menu__link:hover svg {
  fill: rgba(var(--cl-a), 1);
}

.work-section {
  padding-top: clamp(15px, 1.9231vw, 25px);
}
.work-section__box {
  display: grid;
}
@media (min-width: 1240px) {
  .work-section__box {
    grid-gap: clamp(25px, 5.3077vw, 69px);
  }
}
@media (max-width: 1239px) {
  .work-section__box {
    grid-gap: 30px;
  }
}
@media (min-width: 1024px) {
  .work-section__box {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 1024px) {
  .work-section__box {
    grid-template-columns: 1fr;
  }
}
.work-section__inner {
  display: flex;
  flex-direction: column;
  grid-gap: clamp(25px, 2.6923vw, 35px);
}
@media (max-width: 1024px) {
  .work-section__inner {
    order: -1;
  }
}

.terms-section {
  position: relative;
  padding: clamp(34px, 5.4615vw, 71px) 0 0;
  background-color: rgba(var(--bg-c), 1);
  z-index: 2;
  margin-bottom: clamp(50px, 7.2308vw, 94px);
}
.terms-section::before {
  position: absolute;
  content: "";
  width: 100vw;
  height: calc(var(--header-height) + 5px);
  background-color: rgba(var(--bg-c), 1);
  top: 5px;
  transform: translateY(-100%);
}
@media (max-width: 576px) {
  .terms-section {
    padding-bottom: 0;
  }
}
.terms-section__box {
  display: flex;
  align-items: center;
  position: relative;
  padding-bottom: clamp(50px, 8.3077vw, 108px);
  overflow: hidden;
}
@media (max-width: 576px) {
  .terms-section__box {
    align-items: flex-start;
    flex-direction: column;
  }
}
.terms-section__image {
  z-index: -1;
  position: absolute;
  bottom: 0;
}
@media (min-width: 768px) {
  .terms-section__image {
    right: 14%;
  }
}
@media (max-width: 767px) {
  .terms-section__image {
    bottom: -1%;
  }
}
@media (max-width: 767px) and (min-width: 576px) {
  .terms-section__image {
    right: 5%;
  }
}
@media (min-width: 576px) {
  .terms-section__image {
    width: clamp(204px, 29.2308vw, 380px);
    height: clamp(228px, 32.6154vw, 424px);
  }
}
@media (max-width: 576px) {
  .terms-section__image {
    width: clamp(174px, 35.417vw, 204px);
    height: clamp(194px, 39.583vw, 228px);
    right: 0;
  }
}
@media (max-width: 414px) {
  .terms-section__image {
    width: min(34.783vw, 144px);
    height: min(39.614vw, 164px);
  }
}
.terms-section .main-top {
  max-width: clamp(220px, 43vw, 559px);
  grid-gap: clamp(20px, 3.5385vw, 46px);
}
@media (max-width: 576px) {
  .terms-section .main-top .title {
    max-width: 305px;
  }
}

.request-section {
  padding-bottom: clamp(50px, 4.6154vw, 60px);
}
.request-section__box {
  display: flex;
  flex-direction: column;
  grid-gap: 30px;
}